.certificate-card-link {
  display: block;
  text-decoration: none;
  color: inherit;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  background-color: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  transition: transform 0.25s ease, box-shadow 0.25s ease, border-color 0.25s ease, background-color 0.25s ease;
}

.certificate-card-link:hover {
  transform: translateY(-4px);
  background-color: rgba(255, 255, 255, 0.06);
  border-color: rgba(255, 255, 255, 0.14);
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.28);
}

/* White gradient glow overlay */
.certificate-card-link::after {
  content: "";
  position: absolute;
  inset: -1px; /* cover borders */
  background: radial-gradient(80% 80% at 50% 0%, rgba(255, 255, 255, 0.18), rgba(255, 255, 255, 0) 70%),
              linear-gradient(180deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0));
  opacity: 0;
  transition: opacity 240ms ease;
  pointer-events: none;
}

.certificate-card-link:hover::after {
  opacity: 1;
}

.certificate-card {
  display: flex;
  flex-direction: column;
}

.certificate-image-container {
  position: relative;
  width: 100%;
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
}

.certificate-image {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  object-fit: cover;
}

.certificate-info {
  padding: 16px;
}

.certificate-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 4px;
  color: #ffffff;
}

.certificate-issuer {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
}

/* Focus styles for accessibility */
.certificate-card-link:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.2);
}
